import {Button, Grid} from '@material-ui/core';
import {Box, TextField} from '@material-ui/core';
import React, {useState} from 'react';
import './App.css';

const client = new WebSocket('ws://chat.gben.me/ws');

function App() {
  const [members, setMembers] = useState(0);
  const [board, setBoard] = useState<JSX.Element[]>([]);
  const [message, setMessage] = useState("");

  function addMessage(msg: JSX.Element) {
    setBoard(board.concat([<span style={{"display": "block"}}>{msg}</span>]));
  }

  client.onopen = () => {
    addMessage(<>Connected</>);
  };

  client.onmessage = (msg) => {
    const jsonData = JSON.parse(msg.data);
    if (jsonData.action === "message") {
      addMessage(jsonData.message);
      updateScroll();
    } else if (jsonData.action === "members") {
      setMembers(jsonData.members || 0);
    }
  };

  client.onclose = () => {
    addMessage(<>Connection closed</>);
  };

  function updateScroll() {
    const element = document.getElementById("chatbox");
    if (element) {
      element.scrollTop = element.scrollHeight;
    }
  }

  function handleSend() {
    client.send(JSON.stringify(message));
    setMessage("");
  }

  function handleSubmit(e: React.FormEvent<HTMLFormElement>) {
    e.preventDefault();
    handleSend();
  }

  return (
    <Box style={{
      "height": "100vh",
      "margin": "10px",
    }}>
      <span>{members ? <>Total {members} connected</> : <>No body connected. Perhaps the connection is not stable</>}</span>
      <pre id="chatbox" style={{
        "border": "1px solid black",
        "height": "80%",
        "overflow": "scroll",
      }}><code>{board}</code></pre>
      <form onSubmit={(e) => handleSubmit(e)}>
        <Grid container>
          <Grid item xs={11}>
            <TextField type="text"
                       value={message}
                       helperText="Type message here"
                       onChange={(e) =>
                         setMessage(e.target.value)}
                       fullWidth
            />
          </Grid>
          <Grid item xs={1}>
            <Button type="submit"
                    disabled={message === ""}
                    variant="contained"
                    color="primary"
                    fullWidth>Send</Button>
          </Grid>
        </Grid>
      </form>
    </Box>
  );
}

export default App;
